.toggle-button-div{
    display: flex;
    justify-content: space-between;
    margin: 0 2rem;
}

.toggle-button{
    background: rgba(255, 255, 255, 0.1);
    border: none;
    cursor: pointer;
    padding: 4px 8px;
}

.left-toggle-button{
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
}

.right-toggle-button{
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
}

.selected-button{
    background: #668CFF;
}